import React, { useState } from 'react';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import Editor from '@monaco-editor/react';
import './App.css';

function App() {
  const [packageXmlFile, setPackageXmlFile] = useState(null);
  const [splitFiles, setSplitFiles] = useState([]);
  const [downloadLink, setDownloadLink] = useState(null);
  const [outputXml, setOutputXml] = useState('');
  const [originalXmlContent, setOriginalXmlContent] = useState('');
  const [splitPackagesContent, setSplitPackagesContent] = useState([]);

  const handlePackageXmlChange = async (e) => {
    const file = e.target.files[0];
    setPackageXmlFile(file);
    const content = await readFile(file);
    setOriginalXmlContent(content);
  };

  const handleSplitFilesChange = (e) => {
    setSplitFiles([...e.target.files]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!packageXmlFile || splitFiles.length === 0) {
      alert("Please upload both package.xml and at least one split file.");
      return;
    }

    try {
      const packageXmlContent = await readFile(packageXmlFile);

      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(packageXmlContent, 'text/xml');

      const versionElem = xmlDoc.getElementsByTagName('version')[0];
      if (!versionElem) {
        alert('Invalid package.xml: <version> element not found');
        return;
      }
      const version = versionElem.textContent;
      const types = Array.from(xmlDoc.getElementsByTagName('types'));

      const splitMembers = {};
      for (const file of splitFiles) {
        const fileContent = await readFile(file);
        splitMembers[file.name] = fileContent.split('\n').map(line => line.trim()).filter(Boolean);
      }

      const splitPackages = splitPackageXml(version, types, splitMembers);

      const outputXml = createOutputXml(splitPackages);
      setOutputXml(outputXml);

      const splitPackagesContent = Object.keys(splitPackages).slice(0, 3).map(file => ({
        fileName: file,
        content: splitPackages[file]
      }));
      setSplitPackagesContent(splitPackagesContent);

      createZipFile(splitPackages);
    } catch (error) {
      console.error('Error processing files:', error);
      alert('An error occurred while processing the files. Please check the console for details.');
    }
  };

  const readFile = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (event) => resolve(event.target.result);
      reader.onerror = (error) => reject(error);
      reader.readAsText(file);
    });
  };

  const splitPackageXml = (version, types, splitMembers) => {
    const splitPackages = {};
    for (const file in splitMembers) {
      splitPackages[file] = createPackageXml(version, types.filter(type => {
        const nameElem = type.getElementsByTagName('name')[0];
        return nameElem && splitMembers[file].includes(nameElem.textContent);
      }));
    }
    return splitPackages;
  };

  const createPackageXml = (version, types) => {
    const doc = document.implementation.createDocument('', '', null);
    const packageElem = doc.createElement('Package');
    packageElem.setAttribute('xmlns', 'http://soap.sforce.com/2006/04/metadata');
    const versionElem = doc.createElement('version');
    versionElem.textContent = version;
    packageElem.appendChild(versionElem);

    types.forEach((type) => {
      const typeElem = doc.createElement('types');
      Array.from(type.getElementsByTagName('members')).forEach((member) => {
        const memberElem = doc.createElement('members');
        memberElem.textContent = member.textContent;
        typeElem.appendChild(memberElem);
      });
      const nameElem = doc.createElement('name');
      nameElem.textContent = type.getElementsByTagName('name')[0].textContent;
      typeElem.appendChild(nameElem);
      packageElem.appendChild(typeElem);
    });

    const serializer = new XMLSerializer();
    const roughString = serializer.serializeToString(packageElem);
    const formattedXml = prettifyXml(roughString);
    return `<?xml version="1.0" encoding="UTF-8"?>\n${formattedXml}`;
  };

  const prettifyXml = (xmlString) => {
    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(xmlString, 'application/xml');
    const xsltString = `
      <xsl:stylesheet version="1.0" xmlns:xsl="http://www.w3.org/1999/XSL/Transform">
        <xsl:output method="xml" indent="yes"/>
        <xsl:template match="node()|@*">
          <xsl:copy>
            <xsl:apply-templates select="node()|@*"/>
          </xsl:copy>
        </xsl:template>
      </xsl:stylesheet>`;
    const xsltDoc = parser.parseFromString(xsltString, 'application/xml');
    const xsltProcessor = new XSLTProcessor();
    xsltProcessor.importStylesheet(xsltDoc);
    const resultDoc = xsltProcessor.transformToDocument(xmlDoc);
    const resultString = new XMLSerializer().serializeToString(resultDoc);
    return resultString;
  };

  const createOutputXml = (splitPackages) => {
    return Object.keys(splitPackages).map(file => `=== ${file} ===\n\n${splitPackages[file]}\n\n`).join('\n');
  };

  const createZipFile = (splitPackages) => {
    const zip = new JSZip();
    for (const file in splitPackages) {
      const fileName = file.replace('.txt', '_package.xml');
      zip.file(fileName, splitPackages[file]);
    }

    zip.generateAsync({ type: 'blob' }).then((content) => {
      saveAs(content, 'split_packages.zip');
      setDownloadLink(URL.createObjectURL(content));
    });
  };

  return (
    <div className="App">
      <nav className="bg-gray-800 p-4 text-white">
        <h1 className="text-xl">Salesforce Package Splitter</h1>
      </nav>
      <div className="flex h-screen">
        <div className="w-1/2 p-4 bg-gray-100 overflow-y-auto">
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label className="block text-gray-700">Upload package.xml:</label>
              <input type="file" accept=".xml" onChange={handlePackageXmlChange} className="mt-2 p-2 border border-gray-300" />
            </div>
            <div className="mb-4">
              <label className="block text-gray-700">Upload split files:</label>
              <input type="file" accept=".txt" multiple onChange={handleSplitFilesChange} className="mt-2 p-2 border border-gray-300" />
            </div>
            <button type="submit" className="bg-blue-500 text-white p-2 rounded">Split package.xml</button>
          </form>
          {originalXmlContent && (
            <div className="mb-4">
              <Editor
                height="60vh"
                defaultLanguage="xml"
                value={originalXmlContent}
                theme="vs-dark"
              />
            </div>
          )}
        </div>
        <div className="w-1/2 p-4 bg-white overflow-y-auto">
          {downloadLink && (
            <div className="mb-4">
              <a href={downloadLink} download="split_packages.zip" className="bg-green-500 text-white p-2 rounded">Download Split Packages</a>
            </div>
          )}
    
          {splitPackagesContent.length > 0 && (
            <div className="mb-4">
              {splitPackagesContent.map((splitPackage, index) => (
                <div key={index} className="mb-4">
                  <h3 className="text-md font-medium">{splitPackage.fileName}</h3>
                  <Editor
                    height="40vh"
                    defaultLanguage="xml"
                    value={splitPackage.content}
                    theme="vs-dark"
                    
                  />
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
